
.stripe-z  {
  background: repeating-linear-gradient(
  -55deg,
  #222,
  #222 10px,
  #3e4551 10px,
  #3e4551 20px
);
}

.addDecButton {
  height: 60px;
  margin-bottom: 8px;
}

.addDecButton-btn-scale     {
  transition: transform 0.2s;
}

.addDecButton-btn-scale:hover   {
  transform: scale(1.05);
  background-color: #FFED38;
}

.mar-y  {
  margin: 20px 0 10px 0;
}

.gray-box {
  background-image: linear-gradient(150deg, #3e4551 -30%, #ebfffe 60%, #3e4551);
  border-radius: 1vh;
  height: 60px;
  color: black!important;
}

.decisionCol  {
  padding: 0 80px;
}

.decBodyBack  {
  background-color: #30383A;
}

.someBorder {
  border: 1px solid black;
}

.text-black {
  color: black;
  font-weight: 500;
}

.scrollbar
{
	margin-left: 30px;
	float: left;
	height: 343px;
	width: 100%;
	background: transparent;
	overflow-y: scroll;
	margin-bottom: 25px;
}


.scrollbar-arc
{
	margin-left: 30px;
	float: left;
	height: 441px;
	width: 100%;
	background: transparent;
	overflow-y: scroll;
	margin-bottom: 25px;
}

.force-overflow
{
	min-height: auto;
}

#style-2::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #FFED38;
}

#style-2::-webkit-scrollbar
{
	width: 12px;
  background-color: #F5F5F5;
  border-radius: 1vh;
}

#style-2::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: gray;
}

.roundCorners {
  border-radius: 1vh;
}

.decisionName {
  cursor: pointer;
}

.decisionName:hover {
  background-color: #FCD116;
}

.modalBodyColor {
  background-color: #3e4551
}

.form-control-custom {
  background-color: #3e4551;
  color: white;
  font-weight: 400;
}

.form-control-custom:focus{
  background-color: #3e4551;
  color: white;
  font-weight: 400;
  border-color: turquoise;
  box-shadow: 0 0 0 0.07rem;
}

.form-control-custom::-webkit-input-placeholder  {
  color: white;
  opacity: 0.6;
  font-weight: 300;
}

.form-control-custom:-ms-input-placeholder  {
  color: white;
  opacity: 0.6;
  font-weight: 300;
}

.form-control-custom::-ms-input-placeholder  {
  color: white;
  opacity: 0.6;
  font-weight: 300;
}

.form-control-custom::placeholder  {
  color: white;
  opacity: 0.6;
  font-weight: 300;
}

.colCardPriority  {
  font-size: 15px;
}

.exDecisionBack {
  border-radius: 1vh;
  background-color: #30383A;
}

.fontSmall  {
  font-size: 0.9rem;
}

.borderYellow {
  border: 3px solid #FFED38;
}

.backWhite  {
  background-color: white;
}

.primButton:hover  {
  background-color: #0275d8;
}

.danButton:hover  {
  background-color: #d9534f;
}

.warnButton:hover {
  background-color: #f0ad4e;
}

.expandCardTitle  {
  font-size: 20px;
}

.sm-badge {
  font-size: 12px;
}

.text-aqua {
  color: #00ffff;
}

.botMar {
  margin-bottom: 200px!important;
}

.btn-aquamarine {
  background-color: aqua;
}

.navButtons-box {
  background-image: linear-gradient(150deg, #3e4551 -30%, #ebfffe 60%, #3e4551);
  border-radius: 1vh;
  height: 60px;
}
.jumbotron  {
    height: 400px;
    background-image: linear-gradient(150deg, #3e4551 40%, white 200%);
    color: white;
}

.jumbo-mt-zero  {
    margin-bottom: 0;
}

h1  {
    font-family: "Fira Sans", sans-serif;
    font-weight: 700;
    font-size: 80px;
}       

p {
    font-family: "Arvo", sans-serif;
    font-size: 20px;
}

.fontBold   {
    font-weight: 500;
}

.btn-yellow {
    background-color: #FFED38;
}

html {
    scroll-behavior: smooth;
}

.nav-link   {
    font-weight: 700;
}

.hr-exam  {
    max-width: 80%;
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  }

.a-button    {
    background: none!important;
    border: none;
    padding: 0!important;
    color: #00A5DB;
    font-weight: 500;
    cursor: pointer;
    outline: none!important;
}

.a-button:hover {
    text-decoration: underline;
}

.card-prop  {
    max-width: 35%;
}

.cardBor-radius {
    border-radius: 1%;
}

.headline   {
    font-size: 40px;
}

.card-text  {
    font-size: 1rem;
    font-family: 'Roboto', sans-serif;
}

.cardTitle  {
    font-size: 1.30rem;
    font-weight: 600;
    background-color: #FFED38;
}

.scaleCard  {
    transition: transform 0.2s;
}

.scaleCard:hover    {
    transform: scale(1.05);
}

footer.page-footer {
    bottom: 0;
    color: #fff;
}

.font-small {
    font-size: .9rem;
}

.stylish-color-dark {
    background-color: #3e4551 !important;
}

.sf     {
    text-decoration: none;
    color: black;
}

.sf:hover   {
    text-decoration: underline;
    color: black;
}

.btn-yellow {
    background-color: #FFED38;
}

.roundicon  {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    box-shadow: 0px 5px 5px #3f3f38;
}

.cent   {
    margin-top: 10px;
}

.footer-social-btn  {
    margin: 0 10px;
}


.bs     {
    transition: transform 0.2s;
}

.bs:hover   {
    transform: scale(1.25);
}

footer a    {
    color: white;
    text-decoration: underline;
}

footer p {
    font-size: 15px;
}

.page-enter {
    opacity: 0;
}

.page-enter-active {
    opacity: 1;
    transition: 300ms ease-out;
}

.page-exit {
    opacity: 1;
}

.page-exit-active {
    opacity: 0;
    transition: 300ms ease-in;
}

.navBack    {
    background-color: #FFED38!important
}
